.navbar{
    color:green;
    background-color: wheat;
    position: fixed;
    width: 100%;
    padding:15px;
    display: flex;
    justify-content: space-evenly;
}
.links{
    text-decoration: none;
    margin-right: 20px;
    cursor: pointer;
}
.Name{
    font-size: 35px;
    font-weight: 500;
}
.navOptions{
    font-size: 20px;
}
@media screen and (min-width:320px) and (max-width:640px){
    .navbar{
        width: 100%;
    }
    
    .Name{
        font-size: 50px;
    }
    .navOptions{
        font-size: 28px;
    }
}

